import 'uni-pages';import '@dcloudio/uni-stat';
import Vue from 'vue'
import App from './App'
import Http from './util/http'
import http from './api/api'
import store from './store'
import filters from './filter/filter.js'
import MinRouter from './router/MinRouter'
import minRouter from './router/router'
import uniIcons from './components/uni-icons/uni-icons.vue'
//空数据页面
import mkbEmpty from './components/mkb-empty/mkb-empty.vue'

Vue.use(Http)
Vue.use(MinRouter)

Vue.config.productionTip = false
Object.keys(filters).forEach((key) => { 
	Vue.filter(key, filters[key]) 
});

//注册页面图标
Vue.component('uni-icons', uniIcons)
Vue.component('mkb-empty', mkbEmpty)

Vue.prototype.$store = store

App.mpType = 'app'

const app = new Vue({
  ...App,
  http,
  minRouter
})
app.$mount()